*,
html {
    color: white;
    font-size: 20px;
    scroll-behavior: smooth !important;
    background-color: rgb(22, 22, 22);
    li {
        list-style: none;
    }

    a {
        text-decoration: none;
        color: white;
        cursor: pointer;
    }

    html {
        scroll-behavior: smooth;
    }

    body {
        background-color: rgb(22, 22, 22);
        font-family: Jost, Arial, Helvetica, sans-serif;
        overflow: -moz-hidden-unscrollable;
        scroll-behavior: smooth;

        .main {
            display: flex;
            width: 100%;
            height: 90vh;

            .left-bar {
                display: flex;
                justify-content: center;
                position: relative;
                width: 25%;
                margin: 50px 25px;

                .fixed {
                    display: flex;
                    flex-direction: column;
                    position: fixed;
                    margin: 0 auto;

                    h1 {
                        font-size: 50px;
                        text-align: center;
                        cursor: pointer;
                    }

                    a {
                        padding: 15px;
                        font-size: 16px;
                        text-transform: uppercase;
                        text-align: center;
                        font-weight: 900;
                    }

                    a:hover {
                        background: #ffcc0d;
                    }
                }
            }

            .line {
                background-color: #000;
                width: 8px;
                border-radius: 3px;
                margin-top: 5%;
                margin-bottom: 5%;
            }

            .page {
                position: relative;
                margin: 10px;
            }


            // trang page web
            .page {
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                height: max-content;

                // trang home
                .element {
                    width: 90%;
                    margin-top: 4%;
                    border-bottom: #000 10px solid;
                }

                .myinfo {
                    width: 100%;
                    height: 100vh;
                    background-color: white;
                    display: flex;

                    .myimg {
                        width: 40%;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        .avatar {
                            width: 80%;
                            height: 50%;
                            background-size: cover;
                            background-position: center;
                        }
                    }

                    .content {
                        display: flex;
                        flex-direction: column;
                        width: 100%;
                        align-items: center;
                       
                        h2 {
                            margin-top: 20%;
                            padding: 19px;
                            font-weight: 900;
                            font-size: 30px;
                            border-bottom: #000 10px solid;
                        }

                        p {
                            font-size: 25px;
                        }

                        .social {
                            width: 100%;
                            height: 100px;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            a {
                                margin-left: 5%;

                                img {
                                    padding: 20px;
                                    width: 50px;
                                    height: 50px;
                                }
                            }
                        }
                    }
                }

                //trang skill 

            }
        }
    }
}

html:focus-within {
    scroll-behavior: smooth;
}